import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { SearchBox } from 'office-ui-fabric-react/lib/SearchBox';
import { Nav, INavLink } from 'office-ui-fabric-react/lib/Nav';
import history from 'helpers/history';
import { matchPath, withRouter, RouteComponentProps } from 'react-router-dom';
import { withTranslation, WithTranslation } from 'react-i18next';

import NavData, { NavElementType, NavType } from 'nav';

class Menu extends Component<RouteComponentProps & WithTranslation> {
    state = {
        navData: [],
    };
    componentDidMount() {
        this.createDefaultMenu();
    }
    createDefaultMenu = () => {
        NavData.forEach((e) => {
            e.links.forEach((f) => {
                f.isExpanded = this.hasActive(f);
            });
        });
        this.setState({
            navData: NavData,
        });
    };
    hasActive = (menu: NavElementType): Boolean => {
        let { pathname } = this.props.location;
        if (menu.url) {
            if (matchPath(pathname, menu.url)) {
                menu.collapseByDefault = false;
                return true;
            }
        }
        if (menu.links) {
            return menu.links.some((e) => this.hasActive(e));
        }
        return false;
    };
    onChange = (v: string | undefined) => {
        if (!v) {
            this.setState({ navData: NavData });
            return;
        }
        v = v.toLowerCase();
        let newNavData: Array<NavElementType> = [];
        NavData.forEach((menu) => {
            if (menu.links) {
                menu.links.forEach((m) => {
                    this.checkSubMenu(m, v as string, newNavData);
                });
            }
        });
        this.setState({ navData: [{ links: newNavData }] });
    };

    checkSubMenu = (menu: NavElementType, v: string, newNavData: Array<NavElementType>): void => {
        let menuName = menu?.name;
        if (typeof menuName !== 'string') {
            let _elm = document.createElement('span');
            ReactDOM.render(menuName, _elm);
            menuName = _elm.innerText;
            _elm.remove();
        }
        if (menu.url && menuName.toLowerCase().indexOf(v) > -1) {
            let item: NavElementType = {
                name: menuName,
                url: menu.url,
                key: menu.key,
                icon: menu.icon,
                title: menu.title,
            };
            newNavData.push(item);
        }

        if (menu.links) {
            menu.links.forEach((m) => {
                this.checkSubMenu(m, v, newNavData);
            });
        }
    };
    itemClick = (e: React.MouseEvent<HTMLElement> | undefined, item: INavLink | undefined) => {
        if (item?.url) {
            e?.stopPropagation();
            e?.preventDefault();
            history.push(item.url);
        }
    };
    render() {
        let { navData } = this.state;
        let { t } = this.props;
        return (
            <div className="sidebar">
                <SearchBox
                    className="menu-search"
                    styles={{ root: { width: '100%', borderBottom: '1px solid  #eaeaea', height: 39 } }}
                    placeholder={t('Search')}
                    underlined={true}
                    onChange={(_, newValue) => this.onChange(newValue)}
                />
                <Nav
                    onLinkClick={this.itemClick}
                   
                    styles={{
                        root: { width: '100%', marginTop: 6 },
                        chevronButton: {
                            fontSize: 14,
                            fontWeight: '600',
                        },
                        navItem: {
                            fontSize: 14,
                            fontWeight: '600',
                        },
                        groupContent: { marginBottom: 10 },
                    }}
                    groups={navData}
                />
            </div>
        );
    }
}

export default withTranslation()(withRouter(Menu));
