import React from 'react';
import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import utils from 'helpers/utils';
import history from 'helpers/history';
import queryString from 'query-string';
import ActionBtn from './actionBtn';
import { HMStyles } from './hmStyles';
import { Trans } from 'react-i18next';

export const HM_TABLE_KEY = 'HM_TABLE_CONFIG';
export const HM_DETAIL_TABLE_VIEW = 'HM_DETAIL_TABLE_VIEW';
export const COL_DATA_TYPE = {
    DATE: 'date',
    DATETIME: 'datetime',
    IMAGE: 'image',
};

const buildColumns = function (columns, key, isDesc) {
    const newColumns = columns.slice();
    const currColumn = newColumns.filter((currCol) => key === currCol.key)[0];
    newColumns.forEach((newCol) => {
        if (newCol === currColumn) {
            currColumn.isSortedDescending = isDesc;
            currColumn.isSorted = true;
        } else {
            newCol.isSorted = false;
            newCol.isSortedDescending = true;
        }
    });
    return newColumns;
};
export const renderCheckbox = function () {
    let { selectedAll, keysSelected } = this.state;
    let { primaryKey, onSelected } = this.props;
    let checkboxStyle = selectedAll ? HMStyles.checkboxStyleDisabled : HMStyles.checkboxStyle;

    return {
        key: 'col-checkbox',
        iconName: selectedAll ? 'CheckboxCompositeReversed' : 'Checkbox',
        isIconOnly: true,
        iconClassName: checkboxStyle,
        minWidth: 30,
        maxWidth: 30,
        isResizable: false,
        onColumnClick: () => {
            let newState = !selectedAll;
            this.setState({ selectedAll: newState });
            onSelected(keysSelected, newState);
        },
        onRender: (item) => {
            let checked = selectedAll || keysSelected.indexOf(item[primaryKey]) != -1;
            let k = item[primaryKey];
            let onChange = () => {
                if (keysSelected.indexOf(k) !== -1) {
                    keysSelected.splice(keysSelected.indexOf(k), 1);
                } else {
                    keysSelected.push(k);
                }
                this.setState({
                    keysSelected: keysSelected,
                });
                onSelected(keysSelected, selectedAll);
            };
            return <Checkbox onChange={onChange} disabled={selectedAll} checked={checked} />;
        },
    };
};
export const onPageChange = function (p) {
    let { pageUrl, location } = this.props;
    let params = queryString.parse(location.search);
    params.pageIndex = p;
    let url = queryString.stringify(params);
    pageUrl = pageUrl.startsWith('/') ? pageUrl : `/${pageUrl}`;
    history.push(`${pageUrl}?${url}`);
};

export const onSwitchView = function () {
    let { detailView } = this.state;
    setDetailTableView(!detailView);
    this.setState({
        detailView: !detailView,
    });
};

export const getDetailTableView = () => {
    let view = localStorage.getItem(HM_DETAIL_TABLE_VIEW);
    return !!view;
};
const setDetailTableView = (view) => {
    if (view) {
        localStorage.setItem(HM_DETAIL_TABLE_VIEW, '1');
    } else {
        localStorage.removeItem(HM_DETAIL_TABLE_VIEW);
    }
};

export const itemToList = function (activeItem) {
    let { columns } = this.props;
    return columns.map((e) => {
        return {
            field: e.name,
            onRenderValue: () => {
                return renderRowWithType(e, activeItem);
            },
        };
    });
};
export const onOrderChange = function (_, column) {
    if (column.unOrder) {
        return;
    }
    let { pageUrl, location, order } = this.props;
    let params = queryString.parse(location.search);
    let sortValue;
    params.sort = params.sort || order;
    if (params.sort) {
        let [key, type] = params.sort.split('.');
        if (key == column.key) {
            sortValue = `${column.key}.${type == 'asc' ? 'desc' : 'asc'}`;
        }
    }
    params.sort = sortValue || `${column.key}.desc`;
    let url = queryString.stringify(params);
    history.push(`${pageUrl}?${url}`);
};
const renderRowWithType = (e, item) => {
    if (e.onRender) {
        return e.onRender(item);
    }
    let text = '';
    switch (e.dataType) {
        case COL_DATA_TYPE.DATE:
            text = utils.formatDate(utils.tryGetData(item, e.fieldName));
            break;
        case COL_DATA_TYPE.DATETIME:
            text = utils.formatDate(utils.tryGetData(item, e.fieldName), true);
            break;
        case COL_DATA_TYPE.IMAGE:
            return '';
        default:
            text = utils.tryGetData(item, e.fieldName);
    }
    return <span title={text}>{text}</span>;
};
export const createDefaultColumns = function () {
    let { columns, pageUrl, noActionBtn } = this.props;
    let cols = columns.map((e) => ({
        minWidth: 50,
        isResizable: true,
        isCollapsible: true,
        onColumnClick: this.onOrderChange,
        key: e.fieldName,
        onRender: (item) => {
            let text = '';
            switch (e.dataType) {
                case COL_DATA_TYPE.DATE:
                    text = utils.formatDate(utils.tryGetData(item, e.fieldName));
                    break;
                case COL_DATA_TYPE.DATETIME:
                    text = utils.formatDate(utils.tryGetData(item, e.fieldName), true);
                    break;
                case COL_DATA_TYPE.IMAGE:
                    return '';
                default:
                    text = utils.tryGetData(item, e.fieldName);
            }
            return <span title={text}>{text}</span>;
        },
        ...e,
        maxWidth: getColW(pageUrl, e.fieldName, e.maxWidth || 200),
    }));
    if (!noActionBtn) {
        cols.push({
            name: <Trans>Action</Trans>,
            minWidth: 50,
            maxWidth: 50,
            isResizable: false,
            isCollapsible: false,
            key: 'action-btn',
            onRender: (item) => {
                return <ActionBtn onDelete={() => this.onDelete(item)} onEdit={() => this.onEdit(item)}></ActionBtn>;
            },
        });
    }
    return cols;
};
export const createDefaultDetailModeColumns = function () {
    let { columns, detailModeCols, pageUrl, noActionBtn } = this.props;
    let _cols = detailModeCols || columns;
    let cols = _cols.map((e) => ({
        minWidth: 50,
        isResizable: true,
        isCollapsible: true,
        onColumnClick: this.onOrderChange,
        key: e.fieldName,
        onRender: (item) => {
            let text = '';
            switch (e.dataType) {
                case COL_DATA_TYPE.DATE:
                    text = utils.formatDate(utils.tryGetData(item, e.fieldName));
                    break;
                case COL_DATA_TYPE.DATETIME:
                    text = utils.formatDate(utils.tryGetData(item, e.fieldName), true);
                    break;
                case COL_DATA_TYPE.IMAGE:
                    return '';
                default:
                    text = utils.tryGetData(item, e.fieldName);
            }
            return <span title={text}>{text}</span>;
        },
        ...e,
        maxWidth: getColW(pageUrl, e.fieldName, e.maxWidth || 200),
    }));
    if (!noActionBtn) {
        cols.push({
            name: <Trans>Action</Trans>,
            minWidth: 50,
            maxWidth: 50,
            isResizable: false,
            isCollapsible: false,
            key: 'action-btn',
            onRender: (item) => {
                return <ActionBtn onDelete={() => this.onDelete(item)} onEdit={() => this.onEdit(item)}></ActionBtn>;
            },
        });
    }
    return cols;
};
export const loadData = function () {
    process.nextTick(() => {
        let { apiClient, checkBeforGetData, defaultFilter, order, location } = this.props;
        let { columns } = this.state;
        let query = queryString.parse(location.search);
        let sort = query['sort'] || order;
        query = {
            ...defaultFilter,
            ...query,
            sort: sort,
        };
        if (checkBeforGetData) {
            if (!checkBeforGetData(query)) {
                return;
            }
        }
        this.setState(
            {
                isLoading: true,
                columns: buildColumns(columns, sort.split('.')[0], sort.split('.')[1] == 'desc'),
            },
            () => {
                apiClient
                    .search(query)
                    .then((appData) => {
                        let { data, pageIndex } = appData;
                        if (data && data.length == 0 && pageIndex > 1) {
                            this.onPageChange(pageIndex - 1);
                            return;
                        }
                        this.setState({
                            appData: appData,
                            isLoading: false,
                        });
                    })
                    .catch(() => {
                        this.setState({
                            appData: {},
                            isLoading: false,
                        });
                    });
            },
        );
    });
};

export const onSelectAll = function (all) {
    this.setState({
        selectedAll: all,
    });
    this.props.onSelected(this.state.keysSelected, all);
};

export const onFilterChange = function (k, v, isClearAll) {
    let { pageUrl, location } = this.props;
    pageUrl = pageUrl.startsWith('/') ? pageUrl : `/${pageUrl}`;
    let params = queryString.parse(location.search);
    if (isClearAll) {
        history.push(pageUrl);
        return;
    } else {
        if (v) {
            params[k] = v;
        } else {
            delete params[k];
        }
    }
    let url = queryString.stringify(params);
    history.push(`${pageUrl}?${url}`);
};

const getColW = (pageUrl, key, defaultW) => {
    let data = localStorage.getItem(HM_TABLE_KEY);
    try {
        data = JSON.parse(data);
        return data[pageUrl][key] || defaultW;
    } catch (error) {
        return defaultW;
    }
};
export const onColumnResize = function (col) {
    let { currentWidth, key } = col;
    let { pageUrl } = this.props;
    let data = localStorage.getItem(HM_TABLE_KEY);
    try {
        data = JSON.parse(data) || {};
    } catch (error) {
        data = {};
    }
    data[pageUrl] = data[pageUrl] || {};
    data[pageUrl] = {
        ...data[pageUrl],
        [key]: currentWidth,
    };
    localStorage.setItem(HM_TABLE_KEY, JSON.stringify(data));
};

export const onEdit = function (elm) {
    let { onEdit } = this.props;
    if (onEdit) {
        onEdit(elm);
    }
};
export const onDelete = function (elm) {
    let { onDelete } = this.props;
    if (onDelete) {
        onDelete(elm);
    }
};

export const clearHMConfig = () => {
    localStorage.removeItem(HM_TABLE_KEY);
};
