import React, { Component } from 'react';
import { Persona, PersonaSize, PersonaPresence } from 'office-ui-fabric-react/lib/Persona';
import { ContextualMenu } from 'office-ui-fabric-react/lib/ContextualMenu';
import { IconButton } from 'office-ui-fabric-react';

import { RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import { clearHMConfig, withHotkey } from '_hoangmeo';
import { withTranslation, WithTranslation } from 'react-i18next';

import { Panel, PanelType } from 'office-ui-fabric-react/lib/Panel';
import { APP_HOTKEYS_NAME } from 'helpers/hotkeys';
import BookmarksPanel, { clearBookmarks } from './bookmarksPanel';
import LanguageSelector from './languageSelector';
import AvailableKeys from './availableKeys';
import { RootState } from 'store';
import ThemeSelector from './themeSelector';
const defaultIMG = require('../../assets/images/person-icon.png');

const MENU_ACTION_PROPS = {
    iconName: 'WaffleOffice365',
    styles: {
        root: {
            fontSize: 20,
        },
    },
};
class Header extends Component<RouteComponentProps & WithTranslation & Partial<RootState>> {
    userRef: HTMLAnchorElement | null = null;
    state = {
        showUserContextMenu: false,
        showHotkeyPanel: false,
    };
    handler = {
        [APP_HOTKEYS_NAME.SHOW_AVAILABLE_HOTKEYS]: () => this.showHotkeyPanel(),
    };
    hotkeys = Object.keys(this.handler);
    showUserContextMenu = () => {
        this.setState({
            showUserContextMenu: true,
        });
    };
    hideUserContextMenu = () => {
        this.setState({
            showUserContextMenu: false,
        });
    };
    showHotkeyPanel = () => {
        this.setState({
            showHotkeyPanel: true,
        });
    };
    hideHotkeyPanel = () => {
        this.setState({
            showHotkeyPanel: false,
        });
    };
    render() {
        let { user, t } = this.props;
        let items = [
            {
                key: 'logout',
                text: t('Logout'),
                iconProps: { iconName: 'BlockedSite' },
                onClick: () => console.log('New clicked'),
            },
            {
                key: 'Xóa thiết lập',
                text: t('Clear config'),
                iconProps: { iconName: 'ClearSelectionMirrored' },
                onClick: clearHMConfig,
            },
            {
                key: 'Xóa Bookmarks',
                text: t('Clear bookmarks'),
                iconProps: { iconName: 'History' },
                onClick: clearBookmarks,
            },
            {
                key: 'showHotkeyPanel',
                text: t('Show available hot keys'),
                iconProps: { iconName: 'KeyboardClassic' },
                onClick: this.showHotkeyPanel,
            },
            {
                key: 'change-pass',
                text: t('Change password'),
                iconProps: { iconName: 'LockSolid' },
                onClick: () => console.log('New clicked'),
            },
        ];
        let { showUserContextMenu, showHotkeyPanel } = this.state;
        const examplePersona = {
            imageUrl: defaultIMG,
            text: user?.username,
            secondaryText: user?.role,
            showSecondaryText: true,
        };

        return (
            <div className="header">
                <IconButton className="menu-action" iconProps={MENU_ACTION_PROPS} />
                <div className="app-title">HM Admin</div>
                <div className="account-action">
                    <a onClick={this.showUserContextMenu} ref={(r) => (this.userRef = r)}>
                        <Persona {...examplePersona} size={PersonaSize.size32} presence={PersonaPresence.online} imageAlt={user?.username} />
                    </a>
                    <ContextualMenu
                        items={items}
                        hidden={!showUserContextMenu}
                        target={this.userRef}
                        onItemClick={this.hideUserContextMenu}
                        onDismiss={this.hideUserContextMenu}
                    />
                </div>
                <BookmarksPanel />
                <Panel
                    isLightDismiss
                    type={PanelType.medium}
                    isOpen={showHotkeyPanel}
                    onDismiss={this.hideHotkeyPanel}
                    closeButtonAriaLabel="Close"
                    headerText={'Hot keys'}
                >
                    <AvailableKeys />
                </Panel>
                <LanguageSelector />
                <ThemeSelector />
            </div>
        );
    }
}

export default connect((state: RootState) => {
    return {
        user: state.user,
    };
})(withTranslation()(withHotkey(Header)));
