import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Async from 'react-select/async';
import { withRouter } from 'react-router-dom';
import utils from 'helpers/utils';
import debounce from 'es6-promise-debounce';


class AsyncSelect extends Component {
    static propTypes = {
        url: PropTypes.string.isRequired,
        mappingFunc: PropTypes.func,
        searchKey: PropTypes.string,
        getObjUrl: PropTypes.func,
        getOptionLabel: PropTypes.func.isRequired,
        getOptionValue: PropTypes.func.isRequired,
        placeholder: PropTypes.string,
    };
    static defaultProps = {
        mappingFunc: res => res.data,
    };
    constructor(props) {
        super(props);
        this.state = {
            options: [],
            url: this.props.url,
        };
        this.loadData = this.loadData.bind(this);
    }

    componentDidMount() {
        let { getObjUrl, getOptionValue, defaultValue,apiClient } = this.props;

        if (defaultValue && getObjUrl) {
            let url = getObjUrl(defaultValue);
            apiClient
                .query(url)
                .then(e => {
                    if (utils.tryGetData(this, 'refs.thisRef.select.state')) {
                        this.refs.thisRef.select.state.value = e;
                    }
                })
                .catch(() => {
                    let { lastItem } = this.props;
                    if (defaultValue == getOptionValue(lastItem)) {
                        this.refs.thisRef.select.state.value = lastItem;
                    }
                });
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.url !== this.props.url) {
            this.setState({
                url: nextProps.url,
            });
            this.refs.thisRef.select.state.value = '';
            let { onChange } = this.props;
            if (onChange) {
                onChange();
            }
        }
    }

    loadData(q) {
        let { url } = this.state;
        let { mappingFunc, searchKey, topItem, lastItem  ,apiClient} = this.props;
        let __url = `${url}`;
        if (searchKey) {
            __url = url.indexOf('?') == -1 ? `${url}?${searchKey}=${q || ''}` : `${url}&${searchKey}=${q || ''}`;
        }

        return apiClient.query(__url).then(response => {
            if (mappingFunc) {
                response = mappingFunc(response);
            }
            if (topItem) {
                response = [topItem, ...response];
            }
            if (lastItem) {
                response = [...response, lastItem];
            }
            return response;
        });
    }
    render() {
        let { label } = this.props;
        return (
            <div className={`ms-hm-select `}>
                <label>{label}</label>
                <Async
                    key={this.state.url}
                    {...this.props}
                    loadOptions={debounce(this.loadData, 500)}
                    defaultOptions
                    classNamePrefix="hm-react-select"
                    ref="thisRef"
                />
            </div>
        );
    }
}

export default withRouter(AsyncSelect);
