import { registerHotkey, unRegisterHotkey } from 'reducers/hotkey';
export default CPN => {
    CPN.prototype.componentDidMount = function() {
        this.props.dispatch(registerHotkey(this.handler));
    };

    CPN.prototype.componentWillUnmount = function() {
        this.props.dispatch(unRegisterHotkey(this.hotkeys));
    };
    return CPN;
};
