import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { DATE_ALT_FORMAT } from 'helpers/appConstant';
import { FITLER_TYPE } from './hmFilterComp/type';
import dayjs from 'dayjs';

export default class HMFilterTagLabel extends Component {
    static propTypes = {
        filter: PropTypes.object,
        filterValue: PropTypes.any.isRequired,
    };
    state = {
        value: null,
    };

    componentDidMount() {
        let { filter, filterValue } = this.props;
        let { type } = filter;
        let value = '';
        if (type.type === FITLER_TYPE.ASYNCSELECT) {
            // let { getObjUrl, getOptionLabel } = type.cpn.props;
            // client
            //     .get(getObjUrl(filterValue))
            //     .then(o => {
            //         this.setState({
            //             value: getOptionLabel(o),
            //         });
            //     })
            //     .catch(() => {});
            // return;
        }
        switch (type.type) {
            case FITLER_TYPE.LIST:
                let { list } = type.cpn.props;
                value = list[filterValue];
                break;
            case FITLER_TYPE.DATE:
            case FITLER_TYPE.TEXT:
            case FITLER_TYPE.NUMBER:
                value = filterValue;
        }
        this.setState({ value: value });
    }

    render() {
        let { value } = this.state;
        if (!value) return <noscript></noscript>;
        return <span className="font-weight-500">{value}</span>;
    }
}
