import React from 'react';
import { Toggle } from 'office-ui-fabric-react/lib/Toggle';
import { useTranslation } from 'react-i18next';
import { setAppTheme, loadAppTheme } from 'helpers/utils';
const changeTheme = (enableDark: Boolean) => {
    setAppTheme(enableDark);
};
export default () => {
    let [t] = useTranslation();
    return (
        <div style={{ float: 'right' }}>
            <Toggle
                defaultChecked={loadAppTheme()}
                styles={{ label: { fontWeight: 'inherit' }, root: { padding: 5 } }}
                inlineLabel
                label={t('Dark Mode')}
                onChange={(_, enable) => changeTheme(!!enable)}
            />
        </div>
    );
};
