import React, { Component } from 'react';
import { TextField } from 'office-ui-fabric-react/lib/TextField';
import ListFilter from './list';
import DateFilter from './date';
import AsyncSelect from './asyncSelect';
import { FILTER_OP } from 'helpers/appConstant';
import { FITLER_TYPE } from './type';
export default {
    text: {
        type: FITLER_TYPE.TEXT,
        op: FILTER_OP.TEXT,
        cpn: <TextField />,
        changeFunc: e => e.target.value,
    },
    number: {
        op: FILTER_OP.NUMBER,
        type: FITLER_TYPE.NUMBER,
        cpn: <TextField />,
        changeFunc: e => e.target.value,
    },
    date: {
        op: FILTER_OP.DATE,
        type: FITLER_TYPE.DATE,
        cpn: <DateFilter />,
        changeFunc: v => v,
    },
    list: (list, isDict = false) => ({
        type: FITLER_TYPE.LIST,
        isDict: isDict,
        cpn: <ListFilter list={list} isDict={isDict} />,
        changeFunc: v => v,
    }),
    /**
     * @param props
     * getObjUrl: id => `/province/${id}`,
     * getOptionLabel: ({ code }) => code,
     * getOptionValue: ({ provinceId }) => provinceId,
     * url: '/province',
     * searchKey: 'code',
     * placeholder: 'Chọn Tỉnh/thành phố',
     */
    asyncSelect: props => ({
        type: FITLER_TYPE.ASYNCSELECT,
        cpn: <AsyncSelect {...props} />,
        changeFunc: v => {
            return props.getOptionValue(v);
        },
    }),
};
