import React, { useEffect } from 'react';
import { Panel } from 'office-ui-fabric-react/lib/Panel';
import { useConstCallback } from '@uifabric/react-hooks';
import { ActionButton, IButtonProps } from 'office-ui-fabric-react/lib/Button';
import { findRouteName } from './breadcrumb';
import history from 'helpers/history';
import { useHistory } from 'react-router-dom';
import { Scrollbars } from 'react-custom-scrollbars';
import { useTranslation } from 'react-i18next';

import config from 'appConfig';
import { APP_HOTKEYS_NAME } from 'helpers/hotkeys';
import { registerHotkey, unRegisterHotkey } from 'reducers/hotkey';
import { useDispatch } from 'react-redux';

const BOOKMARKS_KEY = 'BOOKMARKS_KEY';

export const clearBookmarks = () => {
    localStorage.removeItem(BOOKMARKS_KEY);
};

export const getBookmarks = () => {
    try {
        return JSON.parse(localStorage.getItem(BOOKMARKS_KEY) as string) || [];
    } catch (error) {
        return [];
    }
};

export const addBookmark = (url: string) => {
    let bookmarks = getBookmarks();
    bookmarks.unshift(url);
    bookmarks = bookmarks.slice(0, config.HISTORY_LOG_COUNT);
    localStorage.setItem(BOOKMARKS_KEY, JSON.stringify(bookmarks));
};
const getRouterName = (url: string) => {
    url = url.startsWith('/') ? window.location.origin + url : url;
    let uri = new URL(url);
    return findRouteName(uri.pathname) as string;
};

const RenderBookmarkBar = (props: { dismissPanel: Function }) => {
    let { dismissPanel } = props;
    const bookmarks = getBookmarks();

    const onBtnClick = (url: string) => {
        dismissPanel();
        history.push(url);
    };
    return (
        <Scrollbars style={{ height: window.innerHeight - 70 }}>
            {bookmarks.map((url: string, i: Number) => {
                return (
                    <ActionButton
                        styles={{ root: { display: 'block', textAlign: 'left' } }}
                        iconProps={{ iconName: 'History' }}
                        key={`button-${i}`}
                        onClick={() => {
                            onBtnClick(url);
                        }}
                        title={url}
                        text={getRouterName(url)}
                    />
                );
            })}
        </Scrollbars>
    );
};

const TEXT = 'Bookmarks';

const BookmarksPanel = () => {
    const [isOpen, setIsOpen] = React.useState(false);
    const history = useHistory();
    const openPanel = useConstCallback(() => setIsOpen(true));
    const dismissPanel = useConstCallback(() => setIsOpen(false));
    const dispatch = useDispatch();
    let { t } = useTranslation();
    const menuProps = {
        items: [
            {
                key: 'Bookmark this page',
                text: t('Bookmark this page'),
                iconProps: { iconName: 'AddBookmark' },
                onClick: () => addBookmark(history.location.pathname + history.location.search),
            },
        ],
    };
    useEffect(() => {
        dispatch(
            registerHotkey({
                [APP_HOTKEYS_NAME.SHOW_BOOKMARKS_BAR]: openPanel,
            }),
        );
        return () => {
            dispatch(unRegisterHotkey([APP_HOTKEYS_NAME.SHOW_BOOKMARKS_BAR]));
        };
    });

    return (
        <div style={{ float: 'right' }}>
            <ActionButton
                splitButtonMenuProps={{ className: 'split-btn-class' }}
                iconProps={{ iconName: 'Bookmarks' }}
                split
                onClick={openPanel}
                text={t(TEXT)}
                menuProps={menuProps}
            />
            <Panel isLightDismiss isOpen={isOpen} onDismiss={dismissPanel} closeButtonAriaLabel="Close" headerText={t(TEXT)}>
                <RenderBookmarkBar dismissPanel={dismissPanel} />
            </Panel>
        </div>
    );
};

export default BookmarksPanel;
