import React from 'react';
import { getApplicationKeyMap } from 'react-hotkeys';
import { DetailsList, IColumn, CheckboxVisibility } from 'office-ui-fabric-react/lib/DetailsList';

const AvailableKeys = () => {
    const keyMap = getApplicationKeyMap();
    let items = Object.keys(keyMap).map((k) => {
        const { sequences, name } = keyMap[k];
        return {
            key: sequences[0].sequence,
            name: name,
            hotKey: sequences[0].sequence,
        };
    });
    let columns: IColumn[] = [
        { key: '$1', name: 'Shortcut Key', fieldName: 'hotKey', minWidth: 100, maxWidth: 100 },
        { key: '$2', name: 'Description', fieldName: 'name', minWidth: 100 },
    ];
    return <DetailsList items={items} columns={columns} checkboxVisibility={CheckboxVisibility.hidden} />;
};
export default AvailableKeys;
