import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { withRouter } from 'react-router-dom';
import { ActionButton } from 'office-ui-fabric-react';
import { IconButton } from 'office-ui-fabric-react/lib/Button';
import queryString from 'query-string';
import HMFilterTagValue from './hmFilterTagValue';
const rootStyle = {
    root: { color: 'red' },
};

class HMFilterBar extends Component {
    static propTypes = {
        filter: PropTypes.object,
        onFilterChange: PropTypes.func,
        filters:PropTypes.array
    };
    render() {
        let { filters, location, onFilterChange } = this.props;
        let params = queryString.parse(location.search);
        if (!filters) {
            return <noscript></noscript>;
        }
        let values = [];
        filters.forEach(f => {
            if (params[f.key]) {
                values.push({
                    filterValue: params[f.key],
                    filter: f,
                });
            }
        });
        if (values.length == 0) return <noscript></noscript>;
        return (
            <div className="hm-filter-bar">
                <ActionButton
                    onClick={() => onFilterChange(null, null, true)}
                    className="filter-remove-all"
                    styles={rootStyle}
                    iconProps={{ iconName: 'ClearFilter', styles: rootStyle }}
                ></ActionButton>
                <div className="filter-list">
                    {values.map(({ filter, filterValue }) => {
                        return (
                            <div className="filter-list-item" key={filter.key}>
                                <label>
                                    <span className="font-weight-500">{filter.text}</span>&nbsp;=&nbsp;
                                    <HMFilterTagValue key={filterValue} filter={filter} filterValue={filterValue} />
                                </label>
                                <IconButton styles={rootStyle} iconProps={{ iconName: 'RemoveFilter' }} onClick={() => onFilterChange(filter.key, null)} />
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    }
}

export default withRouter(HMFilterBar);
