import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { FocusZone, FocusZoneDirection } from 'office-ui-fabric-react/lib/FocusZone';
import { APP_HOTKEYS_NAME } from 'helpers/hotkeys';
import { withTranslation } from 'react-i18next';

import withHotkey from './withHotkey';
import { connect } from 'react-redux';
class PaginationComponent extends Component {
    static propTypes = {
        onPageChange: PropTypes.func.isRequired,
        pageSize: PropTypes.number.isRequired,
        total: PropTypes.number.isRequired,
        page: PropTypes.number.isRequired,
        className: PropTypes.string,
    };
    static defaultProps = {
        className: 'hm-pagination',
    };
    handler = {
        [APP_HOTKEYS_NAME.LIST_NEXT_PAGE]: () => this.selectPage(this.props.page + 1),
        [APP_HOTKEYS_NAME.LIST_PREV_PAGE]: () => this.selectPage(this.props.page - 1),
        [APP_HOTKEYS_NAME.LIST_SELECT_PAGE]: () => alert('select page'),
        [APP_HOTKEYS_NAME.LIST_GO_TO_FIRST_PAGE]: () => this.selectPage(1),
        [APP_HOTKEYS_NAME.LIST_GO_TO_LAST_PAGE]: () => this.selectPage(Math.ceil(this.props.total / this.props.pageSize)),
    };
    hotkeys = Object.keys(this.handler);
    constructor(props) {
        super(props);
        this.buildArray = this.buildArray.bind(this);
    }
    range(start, end) {
        return Array.from({ length: end - start }, (v, k) => k + start);
    }
    selectPage = p => {
        const { onPageChange } = this.props;
        onPageChange(p);
    };
    buildArray(totalPages, page) {
        if (totalPages > 5) {
            let x = totalPages - page;

            if (x == 0) return this.range(page - 4, page + 1);
            if (x == 1) return this.range(page - 3, page + 2);
            if (x == 2) return this.range(page - 2, page + 3);
            if (page == 1) return this.range(1, page + 5);
            if (page == 2) return this.range(1, page + 4);

            return this.range(page - 2, page + 3);
        }
        return this.range(1, totalPages + 1);
    }
    render() {
        const { onPageChange, pageSize, total, page, className ,t} = this.props;
        let totalpages = Math.ceil(total / pageSize);
        if (!!!totalpages || totalpages <= 1) {
            return <noscript />;
        }
        return (
            <FocusZone direction={FocusZoneDirection.horizontal}>
                <ul className={className}>
                    <li style={{ marginRight: 15, padding: 4 }}>
                        <span>{t("Page")}:</span>
                        <span style={{ marginLeft: 5, fontWeight: 'bold' }}>{`${page}/${totalpages}`}</span>
                    </li>
                    {page > 1 && (
                        <li className="page-item" onClick={() => onPageChange(page - 1)}>
                            <Icon iconName="ChevronLeft" />
                        </li>
                    )}
                    {page > 3 && totalpages > 5 && (
                        <li className="page-item" onClick={() => onPageChange(1)}>
                            1
                        </li>
                    )}

                    {this.buildArray(totalpages, page).map(p => {
                        let css = p == page ? ' page-item active' : 'page-item';
                        return (
                            <li key={p} className={css} onClick={() => onPageChange(p)}>
                                {p}
                            </li>
                        );
                    })}

                    {totalpages > 5 && totalpages - page > 3 && (
                        <li className="page-item" onClick={() => onPageChange(totalpages)}>
                            {totalpages}
                        </li>
                    )}
                    {page < totalpages && (
                        <li className="page-item" onClick={() => onPageChange(page + 1)}>
                            <Icon iconName="ChevronRight" />
                        </li>
                    )}
                    <li style={{ marginLeft: 15, padding: 4 }}>
                        <span>{t("Total")}: </span>
                        <span style={{ fontWeight: 'bold', marginLeft: 5, marginRight: 5 }}>{total}</span>
                    <span>{t("Records")}</span>
                    </li>
                </ul>
            </FocusZone>
        );
    }
}

PaginationComponent = withTranslation()(withHotkey(PaginationComponent));
export default connect()(PaginationComponent);
