import { KeyMap, KeySequence } from 'react-hotkeys';

const createSequences = (name: string, k: string) => <KeySequence>{ name, sequence: `alt+q+${k}` };

export const _ = {
    // all page
    SHOW_BOOKMARKS_BAR: 'HM_SHOW_BOOKMARKS_BAR',
    SHOW_USER_CONTEXT_MENU: 'HM_SHOW_USER_CONTEXT_MENU',
    SHOW_AVAILABLE_HOTKEYS: 'SHOW_AVAILABLE_HOTKEYS',
    // only list screen
    LIST_RELOAD: 'HM_LIST_RELOAD',
    LIST_FIND: 'HM_LIST_FIND',
    LIST_ADD: 'HM_LIST_ADD',
    LIST_NEXT_PAGE: 'HM_LIST_NEXT_PAGE',
    LIST_PREV_PAGE: 'HM_LIST_PREV_PAGE',
    LIST_SELECT_PAGE: 'HM_LIST_SELECT_PAGE',
    LIST_GO_TO_FIRST_PAGE: 'LIST_GO_TO_FIRST_PAGE',
    LIST_GO_TO_LAST_PAGE: 'LIST_GO_TO_LAST_PAGE',
};
const hotkeysConfig: KeyMap = {
    // all page
    [_.SHOW_BOOKMARKS_BAR]: createSequences('Show bookmarks bar', 'b'),
    [_.SHOW_USER_CONTEXT_MENU]: createSequences('Show user context menu', 'u'),
    [_.SHOW_AVAILABLE_HOTKEYS]: createSequences('Show available hot keys', '`'),

    // only list screen
    [_.LIST_ADD]: createSequences('Add new item in list screen', 'a'),
    [_.LIST_FIND]: createSequences('Find items in list screen', 's'),
    [_.LIST_RELOAD]: createSequences('Reload data in list screen', 'f5'),
    [_.LIST_NEXT_PAGE]: createSequences('Next page in list screen', 'n'),
    [_.LIST_PREV_PAGE]: createSequences('Prev page in list screen', 'p'),
    [_.LIST_SELECT_PAGE]: createSequences('Select page in list screen', 'up'),
    [_.LIST_GO_TO_FIRST_PAGE]: createSequences('Go to first page in list screen', '1'),
    [_.LIST_GO_TO_LAST_PAGE]: createSequences('Go to last page in list screen', '0'),
};
// export
export default hotkeysConfig;
export const APP_HOTKEYS_NAME = _;
