import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { CommandBar } from 'office-ui-fabric-react/lib/CommandBar';
import { Dialog, DialogType, DialogFooter } from 'office-ui-fabric-react/lib/Dialog';
import { PrimaryButton, DefaultButton } from 'office-ui-fabric-react/lib/Button';
import queryString from 'query-string';
import { ComboBox } from 'office-ui-fabric-react/lib/index';
import { withRouter } from 'react-router-dom';
import withHotkey from './withHotkey';
import { APP_HOTKEYS_NAME } from 'helpers/hotkeys';
import { withTranslation } from 'react-i18next';

import { FILTER_OP, FILTER_TEXT_MAPPER } from 'helpers/appConstant';
const createMainBtns = (onAddNew, onExport, onSwitchView, otherBtns, t) => {
    let mainBtns = [
        {
            key: 'switch-view',
            text: t('Switch View'),
            cacheKey: 'switch-view',
            iconProps: { iconName: 'RedEye' },
            onClick: onSwitchView,
        },
    ];

    if (onAddNew) {
        mainBtns.push({
            key: 'newItem',
            text: t('Add new'),
            cacheKey: 'myCacheKey',
            iconProps: { iconName: 'Add' },
            onClick: onAddNew,
        });
    }
    if (onExport) {
        mainBtns.push({
            key: 'download',
            text: t('Download'),
            iconProps: { iconName: 'Download' },
            onClick: onExport,
        });
    }
    return otherBtns ? [...mainBtns, ...otherBtns] : mainBtns;
};
class HMCommandBar extends Component {
    state = {
        hideFilterDialog: true,
        currentFilter: null,
        currentFilterValue: null,
        selectedOp: null,
        opOptions: [],
    };
    handler = {
        [APP_HOTKEYS_NAME.LIST_RELOAD]: this.props.reload,
        [APP_HOTKEYS_NAME.LIST_ADD]: this.props.onAddNew,
        [APP_HOTKEYS_NAME.LIST_FIND]: () => this.showFilterDialog(),
    };
    hotkeys = Object.keys(this.handler);
    showFilterDialog = () => {
        this.setState({ hideFilterDialog: false });
    };
    closeFilterDialog = () => {
        this.setState({ hideFilterDialog: true, currentFilter: null, currentFilterValue: null, selectedOp: null, opOptions: [] });
    };

    onFilerTypeChange = currentFilter => {
        let opOptions = [];
        let { filterByOperation } = this.props;
        if (filterByOperation && currentFilter && currentFilter.type.op) {
            opOptions = this.createFilterOP(currentFilter.type.op);
        }
        this.setState({
            currentFilter: currentFilter,
            currentFilterValue: this.getFilterValue(currentFilter.key),
            opOptions: opOptions,
            selectedOp: opOptions.length != 0 ? opOptions[0].key : null,
        });
    };
    onFilterValueChange = (filterType, e, p1, p2, p3) => {
        this.setState({
            currentFilterValue: filterType.changeFunc(e, p1, p2, p3),
        });
    };
    onFilter = () => {
        let { currentFilter, currentFilterValue, selectedOp } = this.state;
        let { filterByOperation } = this.props;
        if (filterByOperation && selectedOp) {
            currentFilterValue = `${selectedOp}.${currentFilterValue}`;
        }
        this.props.onFilterChange(currentFilter.key, currentFilterValue);
        this.closeFilterDialog();
    };
    getFilterValue = k => {
        let { location, filterByOperation } = this.props;
        let params = queryString.parse(location.search);
        let value = params[k];
        if (filterByOperation && value && value.split('.').length == 2) {
            return value.split('.')[1];
        }
        return value;
    };
    createFilterOP = op => {
        return op.map(k => ({
            key: k,
            text: FILTER_TEXT_MAPPER[k],
        }));
    };
    onFilerOpChange = v => {
        this.setState({
            selectedOp: v.key,
        });
    };

    render() {
        let { filters, otherBtns, onAddNew, onExport, reload, onSwitchView, t } = this.props;
        let { currentFilter, currentFilterValue, hideFilterDialog, selectedOp, opOptions } = this.state;
        const filterBtn = {
            key: 'Filter',
            text: t('Search'),
            iconProps: { iconName: 'Filter' },
            onClick: () => this.showFilterDialog(),
        };
        const reloadBtn = {
            key: 'Refresh',
            text: t('Refresh data'),
            iconProps: { iconName: 'Refresh' },
            onClick: reload,
        };
        const hasFIlter = filters && filters.length;

        return (
            <>
                <CommandBar
                    className="hm-command-bar"
                    styles={{ root: { paddingLeft: 0 } }}
                    items={createMainBtns(onAddNew, onExport, onSwitchView, otherBtns, t)}
                    farItems={hasFIlter ? [filterBtn, reloadBtn] : [reloadBtn]}
                />
                {hasFIlter && !hideFilterDialog && (
                    <Dialog
                        className="hm-modal"
                        hidden={this.state.hideFilterDialog}
                        onDismiss={this.closeFilterDialog}
                        dialogContentProps={{
                            type: DialogType.largeHeader,
                            title: t('Search'),
                        }}
                        modalProps={{
                            isBlocking: false,
                            styles: { main: { maxWidth: 450 } },
                        }}
                    >
                        <ComboBox
                            label={t('Filter field')}
                            allowFreeform={false}
                            autoComplete="off"
                            options={filters}
                            onChange={(_, v) => this.onFilerTypeChange(v)}
                        />
                        {opOptions.length != 0 && (
                            <ComboBox
                                label={t('Filter type')}
                                allowFreeform={false}
                                autoComplete="off"
                                options={opOptions}
                                selectedKey={selectedOp}
                                onChange={(_, v) => this.onFilerOpChange(v)}
                            />
                        )}

                        {currentFilter &&
                            React.cloneElement(currentFilter.type.cpn, {
                                onChange: e => this.onFilterValueChange(currentFilter.type, e),
                                defaultValue: this.getFilterValue(currentFilter.key),
                                label: t('Search value'),
                            })}
                        <DialogFooter>
                            <PrimaryButton onClick={this.onFilter} text={t('Search')} disabled={!currentFilter || !currentFilterValue} />
                            <DefaultButton onClick={this.closeFilterDialog} text={t('Close')} />
                        </DialogFooter>
                    </Dialog>
                )}
            </>
        );
    }
}

HMCommandBar.propTypes = {
    filters: PropTypes.array,
    onFilterChange: PropTypes.func,
    reload: PropTypes.func,
    onAddNew: PropTypes.func,
    onExport: PropTypes.func,
    onSwitchView:PropTypes.func
};
HMCommandBar = withTranslation()(withHotkey(HMCommandBar));
export default connect()(withRouter(HMCommandBar));
