import React, { Component, Suspense } from 'react';
import { Route, Switch, withRouter, RouteComponentProps, RouteProps } from 'react-router-dom';
import { connect } from 'react-redux';
import HotkeyRegister from './hotkey';
import { ToastContainer } from 'react-toastify';

import { user_change_info } from 'reducers/user';
import Loading from 'components/loading';

// --------------------
import Header from './Header';
import Menu from './Menu';
import BreakCrumb from './breadcrumb';
import appRoutes from '../../routes';

interface HMRouterProps extends RouteProps {
    name: string;
}
class HMRoute extends Route<HMRouterProps> {}

// --------------------
class Layout extends Component<RouteComponentProps & { dispatch: Function }, { checkUser: Boolean }> {
    state = { checkUser: false };
    componentDidMount() {
        this.props.dispatch(user_change_info({ username: 'HoangMeo', role: 'Admin' }));
        this.setState({ checkUser: true });
    }
    render() {
        let { checkUser } = this.state;
        if (!checkUser) {
            return <Loading></Loading>;
        }
        return (
            <div className="App">
                <ToastContainer />
                <HotkeyRegister />
                <Header></Header>
                <div className="body">
                    <Menu></Menu>
                    <div className="content">
                        <div className="breadcrumb">
                            <BreakCrumb />
                        </div>

                        <div className="content-data">
                            <Suspense fallback={<Loading />}>
                                <Switch>
                                    {appRoutes.map((r, idx) => {
                                        let { exact, name, ...oProps } = r;
                                        return r.component ? (
                                            <HMRoute
                                                key={idx}
                                                path={r.path}
                                                exact={exact}
                                                name={name}
                                                render={(props) => {
                                                    return <r.component {...oProps} {...props} />;
                                                }}
                                            />
                                        ) : null;
                                    })}
                                </Switch>
                            </Suspense>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect()(withRouter(Layout));
