import React from 'react';
import { ActionButton } from 'office-ui-fabric-react';

const style = { root: { height: 20 } };
const ActionBtn = ({ onDelete, onEdit }) => (
    <>
        <ActionButton styles={style} onClick={onEdit} title={'Sửa'} iconProps={{ iconName: 'EditNote' }}></ActionButton>
        <ActionButton styles={style} onClick={onDelete} title={'Xóa'} iconProps={{ iconName: 'Delete', styles: { root: { color: 'red' } } }}></ActionButton>
    </>
);
export default ActionBtn;
