import React, { Component } from 'react';
import PropsTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { ComboBox } from 'office-ui-fabric-react/lib/index';

class SelectField extends Component {
    static propsTypes = {
        list: PropsTypes.oneOfType([PropsTypes.array, PropsTypes.object]),
        isDict: PropsTypes.bool,
    };
    static defaultProps = {
        isDict: false,
    };
    constructor(props) {
        super(props);
        let { defaultValue } = this.props;
        this.state = {
            selectedKey: defaultValue,
        };
    }
    onChange = (e, option) => {
        let { onChange } = this.props;
        this.setState({ selectedKey: option.key });
        onChange(option.value);
    };
    render() {
        let { list, isDict, ...props } = this.props;
        let { selectedKey } = this.state;
        let options;
        if (isDict) {
            let arr = Object.keys(list);
            options = arr.map(k => ({ value: k, text: list[k] }));
        } else {
            options = list.map((e, i) => ({ value: i + '', text: e }));
        }
        options = options.map(e => ({ ...e, key: e.value }));
        return <ComboBox {...props} allowFreeform={false} autoComplete="off" options={options} selectedKey={selectedKey} onChange={this.onChange} />;
    }
}

export default withRouter(SelectField);
