import * as React from 'react';
import { Breadcrumb } from 'office-ui-fabric-react/lib/Breadcrumb';
import { matchPath, withRouter, RouteComponentProps } from 'react-router-dom';
import appRoutes from '../../routes';
import history from 'helpers/history';
import { withTranslation, WithTranslation } from 'react-i18next';

const getPaths = (pathname: string) => {
    const paths = ['/'];
    if (pathname === '/') return paths;
    pathname.split('/').reduce((prev, curr) => {
        const currPath = `${prev}/${curr}`;
        paths.push(currPath);
        return currPath;
    });
    return paths;
};
export const findRouteName = (url: string) => {
    const aroute = appRoutes.find((route) => matchPath(url, { path: route.path, exact: route.exact }));
    return aroute && aroute.name ? aroute.name : null;
};

let AppBreadcrumb: React.FC<RouteComponentProps & WithTranslation> = ({ location, t }) => {
    const paths = getPaths(location.pathname);
    let data = paths.map((url: string) => {
        return {
            text: t(findRouteName(url) as string),
            key: url,
            onClick: () => history.push(url),
        };
    });
    if (data.length == 1) return <noscript />;
    return (
        <Breadcrumb
            styles={{
                itemLink: { fontSize: 14 },
                root: {
                    width: '100%',
                    marginTop: 0,
                },
            }}
            items={data}
            maxDisplayedItems={5}
        />
    );
};

export default withTranslation()(withRouter(AppBreadcrumb));
