import React, { Component } from 'react';
import { connect } from 'react-redux';
import { RootState } from 'store';
import { GlobalHotKeys, KeyMap } from 'react-hotkeys';
import hotkeysMap from 'helpers/hotkeys';

const HotKey: React.SFC<Partial<RootState>> = ({ hotKey }) => {
    return <GlobalHotKeys handlers={hotKey} keyMap={hotkeysMap} allowChanges={true} />;
};

const mapStateToProps = (state: RootState) => ({
    hotKey: state.hotKey,
});

export default connect(mapStateToProps)(HotKey);
