import React, { useState } from 'react';
import { DatePicker } from 'office-ui-fabric-react/lib/DatePicker';
import dayjs from 'dayjs';
import { DATE_ALT_FORMAT, DATE_API_FORMAT, DATE_PICKER_CONFIG } from 'helpers/appConstant';

interface IDateFilterProps {
    defaultValue: any;
    onChange: Function;
}

const DateFilter: React.FunctionComponent<IDateFilterProps> = ({ defaultValue, onChange, ...props }) => {
    let [dateValue, setValue] = useState(defaultValue ? dayjs(defaultValue).toDate() : undefined);
    const onSelectDate = (date: any) => {
        if (date) {
            setValue(dayjs(date).toDate());
            onChange(dayjs(date).format(DATE_API_FORMAT));
        }
    };
    const onFormatDate = (date: any) => {
        return dayjs(date).format(DATE_ALT_FORMAT);
    };
    const onParseDateFromString = (v: any) => {
        return dayjs(v).toDate();
    };
    return (
        <DatePicker
            {...props}
            allowTextInput={true}
            value={dateValue}
            formatDate={onFormatDate}
            parseDateFromString={onParseDateFromString}
            onSelectDate={onSelectDate}
            strings={DATE_PICKER_CONFIG}
            onBlur={(event) => event.preventDefault()}
        />
    );
};

export default DateFilter;
