import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { DetailsList } from 'office-ui-fabric-react/lib/DetailsList';
import Pagination from './pagination';
import { Scrollbars } from 'react-custom-scrollbars';
import { withTranslation, Trans } from 'react-i18next';
import HMCommandBar from './hmCommandBar';
import appConfig from 'appConfig';
import { detailsListStyles, scrollBarStyle } from './hmStyles';
import { createDefaultColumns, loadData, onOrderChange, onPageChange, onFilterChange, renderCheckbox } from './_utilsFunc';
import { onColumnResize, onDelete, onEdit } from './_utilsFunc';
import { onSwitchView, getDetailTableView, createDefaultDetailModeColumns, itemToList } from './_utilsFunc';
import Loading from 'components/loading';
import HMFilterBar from './hmFitlerBar';

// --------------------------------------
const detailItemCol = [
    {
        name: <Trans>Field</Trans>,
        fieldName: 'field',
        minWidth: 150,
        maxWidth: 150,
        key: 'field',
    },
    {
        name: <Trans>Value</Trans>,
        fieldName: 'value',
        key: 'value',
        onRender: item => {
            return item.onRenderValue();
        },
    },
];

class HMTable extends Component {
    constructor(props) {
        super(props);

        this.loadData = loadData.bind(this);
        this.onPageChange = onPageChange.bind(this);
        this.createDefaultColumns = createDefaultColumns.bind(this);
        this.createDefaultDetailModeColumns = createDefaultDetailModeColumns.bind(this);
        this.onOrderChange = onOrderChange.bind(this);
        this.onFilterChange = onFilterChange.bind(this);
        this.renderCheckbox = renderCheckbox.bind(this);
        this.onColumnResize = onColumnResize.bind(this);
        this.itemToList = itemToList.bind(this);
        this.onDelete = onDelete.bind(this);
        this.onEdit = onEdit.bind(this);
        this.onSwitchView = onSwitchView.bind(this);
        this.state = {
            appData: {},
            isLoading: false,
            selectedAll: false,
            keysSelected: [],
            scrollStyle: scrollBarStyle(),
            columns: this.createDefaultColumns(),
            detailView: getDetailTableView(),
            detailModeCols: this.createDefaultDetailModeColumns(),
            activeItem: null,
        };
    }
    onWindowResize = () => {
        this.setState({
            scrollStyle: scrollBarStyle(),
        });
    };
    componentDidMount() {
        let { noInitialRequest, onRef } = this.props;
        if (!noInitialRequest) {
            this.loadData();
        }
        if (onRef) {
            onRef(this);
        }
        window.addEventListener('resize', this.onWindowResize);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.location !== this.props.location) {
            this.loadData();
        }
    }
    componentWillUnmount() {
        let { onRef } = this.props;
        if (onRef) {
            onRef(undefined);
        }
        window.removeEventListener('resize', this.onWindowResize);
    }

    render() {
        let { appData, columns, isLoading, scrollStyle, detailView, detailModeCols, activeItem } = this.state;
        let { pageIndex, pageSize, total, data } = appData;
        let { primaryKey, minWidth, onSelected, filters, ...props } = this.props;
        if (onSelected) {
            columns = [this.renderCheckbox(), ...columns];
        }
        data = data || [];
        let showPaging = pageSize && pageIndex && Math.ceil(total / pageSize) > 1;
        let newStyle = { width: scrollStyle.width, height: showPaging ? scrollStyle.height : scrollStyle.height + 30 };
        return (
            <>
                {isLoading && <Loading></Loading>}
                <Scrollbars style={newStyle}>
                    <HMCommandBar filters={filters} onFilterChange={this.onFilterChange} reload={this.loadData} onSwitchView={this.onSwitchView} {...props} />
                    <HMFilterBar filters={filters} onFilterChange={this.onFilterChange} {...props} />
                    {!detailView && (
                        <DetailsList
                            onColumnResize={this.onColumnResize}
                            viewport={{ width: minWidth }}
                            styles={detailsListStyles}
                            data-is-scrollable="true"
                            items={data}
                            checkboxVisibility={2}
                            columns={columns}
                            getKey={item => (item ? item[primaryKey] : null)}
                        />
                    )}
                    {detailView && (
                        <>
                            <div style={{ width: scrollStyle.width / 2 - 10, float: 'left' }}>
                                <DetailsList
                                    onColumnResize={this.onColumnResize}
                                    viewport={{ width: scrollStyle.width / 2 - 20 }}
                                    styles={detailsListStyles}
                                    data-is-scrollable="true"
                                    items={data}
                                    checkboxVisibility={2}
                                    columns={detailModeCols}
                                    getKey={item => (item ? item[primaryKey] : null)}
                                    onActiveItemChanged={item => this.setState({ activeItem: item })}
                                />
                            </div>
                            {activeItem && (
                                <div style={{ width: scrollStyle.width / 2, float: 'right' }}>
                                    <DetailsList
                                        viewport={{ width: scrollStyle.width / 2 - 20 }}
                                        styles={detailsListStyles}
                                        data-is-scrollable="true"
                                        items={this.itemToList(activeItem)}
                                        checkboxVisibility={2}
                                        columns={detailItemCol}
                                   
                                    />
                                </div>
                            )}
                        </>
                    )}
                </Scrollbars>
                {showPaging && <Pagination pageSize={pageSize} total={total} page={pageIndex} onPageChange={this.onPageChange} />}
            </>
        );
    }
}
HMTable.contextTypes = {
    store: PropTypes.object,
};
HMTable.propTypes = {
    pageUrl: PropTypes.string.isRequired,
    columns: PropTypes.arrayOf(
        PropTypes.shape({
            name: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
            fieldName: PropTypes.string.isRequired,
            dataType:PropTypes.string
        }),
    ).isRequired,
    detailModeCols: PropTypes.arrayOf(
        PropTypes.shape({
            name: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
            fieldName: PropTypes.string.isRequired,
        }),
    ),
    // action
    onEdit: PropTypes.func,
    onDelete: PropTypes.func,
    onRenderBody: PropTypes.func,
    onRenderTr: PropTypes.func,
    onAddNew: PropTypes.func,
    onExport: PropTypes.func,
    //  api config
    primaryKey: PropTypes.string.isRequired,
    apiClient:PropTypes.object.isRequired,
    order: PropTypes.string,
    checkBeforGetData: PropTypes.func,
    noInitialRequest: PropTypes.bool,
    defaultFilter: PropTypes.string,
    noActionBtn: PropTypes.bool,
    minWidth: PropTypes.number,
    filters: PropTypes.array,

    transformData: PropTypes.func,
    filterByOperation: PropTypes.bool,
};

HMTable.defaultProps = {
    detailModeCols: [],
    minWidth: window.innerWidth - 270,
    noInitialRequest: false,
    noActionBtn: false,
    // change this
    filterByOperation: appConfig.filterByOperation,
    transformData: appConfig.transformData,    
    primaryKey:"id",
    order: 'id,desc',
};
export default withTranslation()(withRouter(HMTable));
